<template>
    <div class="">
        <v-snackbar
            top
            v-model="snackBar"
            :timeout="timeout"
            align="center">
            The settings are saved.
        </v-snackbar>
        <v-dialog
            v-model="getStatusDialogOption"
            persistent
            width="600"
            @click:outside="setDialogFalse()">

            <v-card>
                <v-card-title class="option-title headline text-uppercase d-flex justify-center">
                    <v-icon color="black" class="mr-1">mdi-tools</v-icon>
                    <b>game option</b>
                </v-card-title>
                <v-divider></v-divider>
                <v-card-text>
                    <validation-observer ref="observer" v-slot="">
                    <v-form>
                        <v-container>
                            <v-row>
                                <v-col
                                    cols="12"
                                    md="4">
                                    <validation-provider
                                        v-slot="{ errors }"
                                        name="Number Card"
                                        rules="required|minmax:2,50">
                                        <v-text-field
                                            v-model="form.numberCard"
                                            type="number"
                                            label="Total Card"
                                            :error-messages="errors"
                                            required
                                            ></v-text-field>
                                    </validation-provider>
                                </v-col>

                                <v-col
                                    cols="12"
                                    md="4">
                                    <validation-provider
                                        v-slot="{ errors }"
                                        name="Number Trap Card"
                                        rules="required|minmax:1,49">
                                        <v-text-field
                                            v-model="form.numberTrapCard"
                                            type="number"
                                            label="Trap Card"
                                            :error-messages="errors"
                                            required
                                            ></v-text-field>
                                    </validation-provider>
                                </v-col>
                            </v-row>
                            <div class="" v-if="errorTextStatus == true"
                                style="color: #f44336;">
                                {{ errorText }}
                            </div>
                        </v-container>
                    </v-form>
                    </validation-observer>
                </v-card-text>

                <v-divider></v-divider>

                <v-card-actions>
                    <v-spacer></v-spacer>
                    <v-btn
                        color="success"
                        class="text-uppercase"
                        @click="saveOption()">
                        save
                    </v-btn>
                    <v-btn
                        color="error"
                        class="text-uppercase"
                        @click="resetOption()">
                        reset
                    </v-btn>
                    <v-btn
                        color="white"
                        class="text-uppercase"
                        @click="setDialogFalse()">
                        close
                    </v-btn>
                </v-card-actions>
            </v-card>
        </v-dialog>
    </div>
</template>


<style scoped>
    .error-message {
        color: #ff5252;
    }
    .option-title {
        background-color: #F8F8F8;
        text-align: center;
    }
</style>

<script>
    import { mapGetters, mapActions } from 'vuex'

    import { required } from 'vee-validate/dist/rules'
    import { extend, ValidationObserver, ValidationProvider, setInteractionMode } from 'vee-validate'

    setInteractionMode('eager')

    extend('required', {
        ...required,
        message: '{_field_} can not be empty',
    })

    extend('minmax', {
        validate(value, { min, max }) {
            return value >= parseInt(min) && value <= parseInt(max);
        },
        params: ['min', 'max'],
        message: '{_field_} must more than {min} and less than {max}.',
    });

    extend('maxtrap', {
        validate(value, { max }) {
            console.log(max)
            return value >= parseInt(max);
        },
        params: ['max'],
        message: 'error cuk',
    });


    export default {
        name: 'DialogOption',

        created() {
            this.setFormOption()
        },

        mounted() {
        },

        destroyed() {
        },

        components: {
            ValidationProvider,
            ValidationObserver,
        },

        data: () => ({
            snackBar: false,
            timeout: 2000,
            form: {
                numberPlayer: 0,
                numberCard: 0,
                numberTrapCard: 0,
            },
            errors: null,
            errorTextStatus: false,
            errorText: 'Number of Trap Card must be less than Number Card.',
        }),

        computed: {
            ...mapGetters({
                getStatusDialogOption: 'getStatusDialogOption',
                getPlayers: 'getPlayers',
                getNumberPlayers: 'getNumberPlayers',
                getNumberCard: 'getNumberCard',
                getNumberTrapCard: 'getNumberTrapCard',
            }),
        },

        methods: {
            ...mapActions({
                setStatusDialogOption: 'setStatusDialogOption',
                setPlayers: 'setPlayers',
                setNumberPlayer: 'setNumberPlayer',
                setNumberCard: 'setNumberCard',
                setNumberTrapCard: 'setNumberTrapCard',
                setOption: 'setOption',
            }),
            setDialogFalse(){
                this.setStatusDialogOption(false)
            },
            setFormOption(){
                // this.form.numberPlayer = this.getNumberPlayers
                this.form.numberCard = this.getNumberCard
                this.form.numberTrapCard = this.getNumberTrapCard
            },
            saveOption(){
                this.$refs.observer.validate().then(success => {
                    if (success) {
                        let checkTrapCard = parseInt(this.form.numberTrapCard) >= parseInt(this.form.numberCard) ? false : true;
                        if (checkTrapCard) {
                            this.errorTextStatus = false
                            this.snackBar = true
                            this.setOption(this.form)
                            this.setDialogFalse()
                        }
                        else {
                            this.errorTextStatus = true
                        }
                    }
                });
            },
            resetOption(){
                this.form.numberCard = 2
                this.form.numberTrapCard = 1
                this.$refs.observer.reset()
            },
        },
    }
</script>
