                            <div class="card-inner"
                                :class="item.isFlip == true ? 'flip-card' : '' ">
                                <div class="card-contain card-front d-flex justify-center align-center text-uppercase">
                                    <v-icon color="white" class="mr-1" style="font-size: 7em;">mdi-cards</v-icon>
