<script>
    import { mapGetters, mapActions } from 'vuex'
    export default {
        name: 'DialogPlayGround',

        mounted: function() {
        },

        destroyed: function() {
        },

        data: () => ({
            fullscreen: false,
        }),

        computed: {
            ...mapGetters({
                getStatusDialogPlayground: 'getStatusDialogPlayground',
                getNumberPlayers: 'getNumberPlayers',
                getNumberCard: 'getNumberCard',
                getNumberTrapCard: 'getNumberTrapCard',
                getIsStart: 'getIsStart',
                getDataCards: 'getDataCards',
            }),
        },

        watch: {
        },

        methods: {
            ...mapActions({
                setStatusDialogPlayground: 'setStatusDialogPlayground',
                setIsStart: 'setIsStart',
                setDataCard: 'setDataCard',
                setFlip: 'setFlip',
            }),
            setDialogFalse(){
                this.setStatusDialogPlayground(false)
            },
            flipCard(i){
                this.setFlip(i)
            },
            resetGame(i){
                this.setIsStart(false)
                this.setDataCard(i)
            }
        },
    }
</script>
